import PromoCode from '../apis/promocode';

const state = {
    promoCodes: []
};
const actions = {
    updatePromoCode({ commit }, data) {
        return PromoCode.show(data).then(response => {
            if (response.data.code == 200) {
                commit('updatePromoCode', response.data.Result);
            }
            return response.data;
        })
    }
};
const mutations = {
    updatePromoCode(state, promoCodes) {
        state.promoCodes = [...promoCodes];
    }
};
const getters = {
    promoCodes: state => state.promoCodes
}

export const promocode = {
    namespaced: true,
    state,
    actions,
    mutations,
    getters
};